/*------------------------------------------*/
/*  #CONTACTS
/*------------------------------------------*/

.contacts {
  position: relative;
  width: 100%;

  @media only screen and (min-width: 62em) {
    height: 640px;
  }

  .container {

    @media only screen and (min-width: 62em) {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: 2;
    }
  }
}

.map {
  height: 350px;

  @media only screen and (min-width: 62em) {
    height: 100%;
  }

}

.contacts-block {
  width: 100%;
  max-width: 460px;
  padding-top: 60px;
  padding-bottom: 60px;

  @media only screen and (min-width: 62em) {
    position: absolute;
    top: 90px;
    left: 15px;
    padding: 30px 40px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0px 5px 11.16px 0.84px rgba(0, 0, 0, 0.2);
    z-index: 3;
  }
}

.contacts-title {
  padding-top: 0;
  padding-bottom: 12px;
  margin-bottom: 20px;

  &::after {
    position: absolute;
    content: '';
    top: auto;
    bottom: 0;
    left: 0;
    width: 68px;
    border-top: 3px solid $green;
  }
}

.contacts-item {
  position: relative;
  padding: 16px 0 16px 36px;
  font-size: rem(18);

  &::after {
    position: absolute;
    content: '';
    left: 0;
    top: 18px;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
  }
}

.contacts-item:not(:first-child) {
  border-top: 1px solid #e0e0e0;
}

.contacts-item--phone::after {
  width: 22px;
  height: 22px;
  background-image: url(../img/icons/call.svg);
}

.contacts-item--email::after {
  top: 22px;
  width: 25px;
  height: 20px;
  background-image: url(../img/icons/mail.svg);
}

.contacts-item--address {
  
  &::after {
    width: 20px;
    height: 27px;
    background-image: url(../img/icons/place.svg);
  }

  span {
    color: #888;
  }
}